function sliderOrganization() {
    var swiper = new Swiper('.organization .swiper', {
        slidesPerView: '6',
        spaceBetween: 32,
        loop: false,
        navigation: {
            nextEl: '.organization .swiper-button-next',
            prevEl: '.organization .swiper-button-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            400: {
                slidesPerView: 2,
                spaceBetween: 15,
            },
            575: {
                slidesPerView: 3,
                spaceBetween: 20,
            },
            768: {
                slidesPerView: 4,
                spaceBetween: 20,
            },
            992: {
                slidesPerView: 5,
                spaceBetween: 20,
            },
            1200: {
                spaceBetween: 30,
                slidesPerView: 6,
            },
        }
    })
}

$(document).ready(function() {

    sliderOrganization();

    // Hover-Click
    $(".header__item").hover(onIn, onOut);
    $(".header__item .header__item-show").click(function() {
        if ($(this).parents(".header__item").hasClass("header__item--active")) {
            $(this).parents(".header__item").removeClass("header__item--active")
        } else {
            $(this).parents(".header__item").addClass("header__item--active")
        }
    })

    // Burger
    $('.header__burger').click(function () {
        $(this).toggleClass('header__burger--active')
        $('.header__right').toggleClass('header__right--active')
        $('body').toggleClass('fixed-body')
    })

    // Accordeons
    $(".services__item").hover((function() {
        $(this).toggleClass("services__item--active")
        $(this).children(".services__item-hidden").toggleClass('services__item-hidden--active')
    }));

    $(".info2__item-show").click((function() {
        $(this).parents('.info2__item').toggleClass("info2__item--active")
        $(this).siblings(".info2__item-hidden").slideToggle()
    }));


})

function onIn() {
    if (window.innerWidth > 991) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("header__item--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 991) {
        $(this).removeClass("header__item--active")
    }
}